<template>
  <div class="pageContent" v-loading="loading" element-loading-text="拼命加载中" element-loading-spinner="el-icon-loading">
    <!-- 未授权状态下 -->
    <div v-if="boxType">
      <el-card style="padding:40px 0;margin-top:20px;">
        <div class="head">绑定微信公众号,集沉淀、运营、唤醒粉丝功能于一体,提高转化率</div>
        <div class="flex-row-around">
          <el-card class="conBox" shadow="hover">
            <div class="flex-row-center">
              <img class="bindimg" src="../../../assets/images/wechat/bindWechat.png" alt="">
            </div>
            <div class="flex-row-center mt20">
              <el-button type="primary" @click="bindMp">已有公众号，立即绑定</el-button>
            </div>
            <div class="tac mt20">为保证所有功能的正常使用</div>
            <div class="tac">授权时请把所有权限同意授权给筷赚</div>
          </el-card>
          <el-card class="conBox" shadow="hover">
            <div class="flex-row-center">
              <img class="bindimg" src="../../../assets/images/wechat/newWechat.png" alt="">
            </div>
            <div class="flex-row-center mt20">
              <el-button type="primary" plain @click="openPage('https://mp.weixin.qq.com/')">没有公众号，立即申请</el-button>
            </div>
            <div class="tac mt20">请在微信公众平台申请新的公众号</div>
            <div class="tac">成功后在到筷赚进行绑定</div>
          </el-card>
        </div>
      </el-card>
      <!-- 帮助中心 -->
      <el-card shadow="hover" style="margin-top:20px;">
        <div class="flex-row-around">
          <div class="li flex-row-start">
            <img src="../../../assets/images/wechat/help1.png" alt="">
            <el-button class="btn" type="text">公众号绑定教程</el-button>
          </div>
          <div class="li flex-row-start">
            <img src="../../../assets/images/wechat/help2.png" alt="">
            <el-button class="btn" type="text">公众号绑定注意事项</el-button>
          </div>
          <div class="li flex-row-start">
            <img src="../../../assets/images/wechat/help3.png" alt="">
            <el-button class="btn" type="text">不同公众号类型的功能区别</el-button>
          </div>
        </div>
      </el-card>
    </div>
    <!-- 已授权状态下 -->
    <el-card v-else>
      <div class="flex-row-start">
        <div class="shopName">{{form.nickName}}</div>
        <el-tag type="danger" v-if="form.verifyTypeInfo == -1">未认证</el-tag>
        <el-tag type="success" v-if="form.verifyTypeInfo == 0">微信认证</el-tag>
        <el-tag type="success" v-if="form.verifyTypeInfo == 1">新浪微博认证</el-tag>
        <el-tag type="success" v-if="form.verifyTypeInfo == 2">腾讯微博认证</el-tag>
        <el-tag type="warning" v-if="form.verifyTypeInfo == 3">已资质认证通过但还未通过名称认证</el-tag>
        <el-tag type="warning" v-if="form.verifyTypeInfo == 4">已资质认证通过、还未通过名称认证，但通过了新浪微博认证</el-tag>
        <el-tag type="success" v-if="form.verifyTypeInfo == 5">已资质认证通过、还未通过名称认证，但通过了腾讯微博认证</el-tag>
      </div>
      <el-divider></el-divider>
      <div class="pageHead">基本信息</div>
      <div class="flex-row-between">
        <div>
          <el-form ref="form" :model="form" label-width="140px">
            <el-form-item label="Appid:">
              <div>
                <span>{{form.authorizerAppid}}</span>
                <el-button class="cobyOrderSn" :data-clipboard-text="form.authorizerAppid" @click="copyLink" icon="el-icon-document-copy" type="text"></el-button>
              </div>
            </el-form-item>
            <el-form-item label="微信号:">
              <div>
                <span>{{form.alias ? form.alias : '-'}}</span>
                <el-button v-if="form.alias" class="cobyOrderSn" :data-clipboard-text="form.alias" @click="copyLink" icon="el-icon-document-copy" type="text"></el-button>
              </div>
            </el-form-item>
            <el-form-item label="主体名称:">
              <div>{{form.principalName}}</div>
            </el-form-item>
          </el-form>
        </div>
        <div class="flex-column-center">
          <div>
            <el-image :src="form.qrcodeUrl" style="width: 150px; height: 150px"  fit="fill"></el-image>
          </div>
          <div>
            <el-link :href="form.qrcodeUrl" target="_blank">下载</el-link>
          </div>
        </div>
      </div>
      <el-divider></el-divider>
      <div class="flex-row-start">
        <!-- <div class="btns flex-row-start">
          <img class="btnImg" src="../../../assets/images/wechat/btns1.png" alt="">
          <span class="btnSpan">解除绑定</span>
        </div> -->
        <div class="btns flex-row-start" @click="reAuth">
          <img class="btnImg" src="../../../assets/images/wechat/btns2.png" alt="">
          <span class="btnSpan">重新授权</span>
        </div>
        <div class="btns flex-row-start" @click="showListFun">
          <img class="btnImg" src="../../../assets/images/wechat/btns3.png" alt="">
          <span class="btnSpan">已授权列表</span>
        </div>
      </div>
    </el-card>
    <!-- 授权结果弹框 -->
    <el-dialog title="授权结果" :visible.sync="showDia" width="30%" center>
      <span slot="footer" class="dialog-footer">
        <el-button @click="$router.go(0)">授权失败</el-button>
        <el-button type="primary" @click="$router.go(0)">授权成功</el-button>
      </span>
    </el-dialog>
    <!-- 授权列表弹框 -->
    <el-dialog title="授权列表" :visible.sync="showList" width="70%">
      <div class="flex-row-between flex-wrap">
        <div class="liItem flex-row-start" v-for="(item,index) in list" :key="index">
          <i style="color:#409EFF;" v-if="item.selected == 1" class="el-icon-circle-check"></i>
          <i style="color:#F56C6C;" v-if="item.selected == 0" class="el-icon-circle-close"></i>
          <span style="margin-left:10px;">{{item.authName}}</span>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getAuthUrl , findShopMpAuth } from '@/api/weChat/mpManage'
import { findShopMpWechat } from '@/api/moneyConfig/payConfig'

export default {
  data(){
    return{
      page:'',                  // 新打开页面的链接
      form:{},                  // 授权状态下的数据             
      boxType:false,            // 是否授权
      loading:true,             // 全局loading
      showDia:false,            // 授权结果弹出框
      showList:false,           // 权限列表弹出框
      list:[],                  // 授权列表
    }
  },
  created(){
    this.getData();
    this.getLink();
  },
  methods:{
    showListFun(){
      this.getList();
      this.showList = true
    },
    // 获取授权地址
    getLink(){
      getAuthUrl(this.shop.shopId,1).then(res=>{
        console.log('公众号授权地址',res)
        this.page = res.data
      })
    },
    // 获取数据
    getData(){
      findShopMpWechat(this.shop.shopId,1).then(res=>{
        console.log('查询店铺是否认证',res)
        if(res.data == null){
          console.log('未授权')
          this.boxType = true;
        }else{
          this.boxType = false;
          this.form = res.data;
          console.log('授权了')
        }
        this.loading = false;
      })
    },
    // 获取授权列表
    getList(){
      findShopMpAuth(this.shop.shopId,1).then(res=>{
        console.log('授权列表',res)
        if(res.code == 200){
          this.list = res.data
        }
      })
    },
    reAuth(){
      const h = this.$createElement;
      this.$msgbox({
        title: '请您注意',
        message: h('p', null, [
          h('div', null, '1.重新授权时不可换绑其他微信公众号,否则重新授权将失败。 '),
          h('div', null, '2.为保证您在筷赚平台功能的正常使用,授权时请保持默认选择,把开房平台账号管理权限统一授权给筷赚。 '),
        ]),
        showCancelButton: true,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
      }).then(action => {
        this.openPage(this.page)
        this.showDia = true
      }).catch(() => {});
    },
    // 绑定公众号
    bindMp(){
      this.showDia = true;
      this.openPage(this.page)
    },
    // 已有
    openPage(page){
      window.open(page, "_blank");
    },
    // 复制链接
    copyLink() {
      let clipboard = new this.clipboard('.cobyOrderSn');
      console.log(clipboard)
      clipboard.on('success', () => {
        this.$message({
          message: '复制成功',
          type: 'success'
        });
      });
      clipboard.on('error', () => {
        this.$message.error('复制失败');
      });
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .liItem{
    width: 48%;
    line-height: 40px;
    font-size: 14px;
  }
  .shopName{
    font-size: 18px;
    font-weight: bold;
    margin-right: 20px;
  }
  .cobyOrderSn{
    margin-left: 10px;
  }
  .btns{
    cursor: pointer;
    margin-right: 50px;
    .btnImg{
      width: 40px;
      height: 40px;
    }
    .btnSpan{
      font-size: 14px;
      margin-left: 8px;
    }
    &:hover{
      .btnSpan{
        color: #409EFF;
      }
    }
  }
  .mt20{
    margin-top: 20px;
  }
  .tac{
    text-align: center;
    font-size: 14px;
    color: #909399;
  }
  .head{
    width: 100%;
    text-align: center;
    font-weight: bold;
    font-size: 18px;
    color: #303133;
    padding-bottom: 60px;
  }
  .conBox{
    padding: 60px 0;
    width: 45%;
    .bindimg{
      width: 80px;
      height: 80px;
    }
  }
  .li{
    .btn{
      color:#303133;
      margin-left: 10px ;
      &:hover{
        color: #409EFF;
      }
    }
    img{
      width: 40px;
      height: 40px;
      vertical-align: top;
    }
  }
</style>